.moving-circles {
  pointer-events: none;
  z-index: -1;
  position: absolute;
  inset: 0;
}

.moving-circle {
  background-color: #f2c14e;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  animation: 5s linear infinite both moveCircle;
  position: absolute;
}

@keyframes moveCircle {
  0% {
    transform: translate(-50%, -50%);
  }

  25% {
    transform: translate(300%, -50%);
  }

  50% {
    transform: translate(700%, 100%);
  }

  75% {
    transform: translate(500%, 450%);
  }

  100% {
    transform: translate(-50%, -50%);
  }
}

